@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&display=swap);
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}*{font-family:"Roboto", sans-serif}body{background-color:#737373}.container{max-width:1280px;margin:0 auto}

.hero{background-image:linear-gradient(to top, rgba(0,0,0,0.5), rgba(0,0,0,0.5)),url(/my-app-vr84-bootcamp/static/media/hero-bg.5d86912d.jpg);background-size:cover;padding:80px 0 178px}.hero-partners{display:flex;align-items:center}.hero-partners-item{margin-left:60px}.hero-partners-item:first-child{margin-left:-40px}.hero-navigation{margin-top:45px}.hero-navigation-item{text-decoration:none;font-weight:normal;font-size:19px;line-height:22px;color:#fff;margin-left:74px}.hero-navigation-item:first-child{margin-left:0}.hero-title{width:100%;max-width:664px;font-weight:normal;font-size:57px;line-height:67px;color:#fff;margin:108px 0 132px}.hero-link{display:inline-block;padding:35px 151px;text-decoration:none;font-weight:normal;font-size:32px;line-height:37px;color:#fff;background:#b78a3f}

.facts{box-sizing:border-box;position:relative;padding:100px 60px 50px}.facts-title{font-weight:normal;font-size:35px;line-height:41px;text-align:center;color:#fff;margin-bottom:30px}.facts-slide-desc{margin-top:40px;font-weight:normal;font-size:27px;line-height:32px;text-align:center;color:#fff}.facts>.swiper-button-prev,.facts .swiper-button-next{color:#B78A3F;top:42%}.facts>.swiper-button-prev{left:10px}.facts>.swiper-button-next{right:10px}.facts>.swiper-button-disabled{opacity:0}.facts-pagination{display:flex;justify-content:center;margin-top:66px}.facts-pagination-bullet{display:inline-block;width:24px;height:24px;border-radius:50%;margin:0 20px;background-color:transparent;border:2px solid #B78A3F;opacity:1;transition:0.3s background-color}.facts-pagination>.swiper-pagination-bullet-active{background-color:#B78A3F}

.sectors{padding:50px 0}.sectors-title{font-weight:normal;font-size:35px;line-height:41px;color:#fff;text-align:center}.sectors-subtitle{font-weight:normal;font-size:23px;line-height:27px;color:#ffffff;margin:42px 0 30px}.sectors-list{display:flex;flex-wrap:wrap;justify-content:space-between}.sectors-list-item{position:relative;width:calc((100% / 3) - 70px);height:240px;display:flex;align-items:center;margin-bottom:30px}.sectors-list-item-name{width:100%;text-align:center;font-size:23px;line-height:30px;color:#222222;background:rgba(255,255,255,0.83)}.sectors-list-item-tooltip{position:absolute;left:100%;width:440px;pointer-events:none;-webkit-appearance:none;-moz-appearance:none;appearance:none;background-color:#dadada;padding:16px 16px 16px 75px;-webkit-clip-path:polygon(100% 0, 100% 100%, 75px 100%, 0 50%, 75px 0);clip-path:polygon(100% 0, 100% 100%, 75px 100%, 0 50%, 75px 0);z-index:1;-webkit-animation:fadeIn 0.4s ease-in-out;animation:fadeIn 0.4s ease-in-out}.sectors-list-item-tooltip-switched{left:auto;left:initial;right:100%;-webkit-clip-path:polygon(calc(100% - 75px) 0, 100% 50%, calc(100% - 75px) 100%, 0 100%, 0 0);clip-path:polygon(calc(100% - 75px) 0, 100% 50%, calc(100% - 75px) 100%, 0 100%, 0 0);padding:16px 75px 16px 16px}@-webkit-keyframes fadeIn{0%{opacity:0}100%{opacity:1}}@keyframes fadeIn{0%{opacity:0}100%{opacity:1}}

.footer{border-top:1px solid #ffffff}.footer-partners{display:flex;align-items:center;margin-top:-40px}.footer-partners-item{margin-right:120px}.footer-partners-item:first-child{margin-left:-40px}.footer-partners-item:last-child{margin-right:0}.footer-text-wrap{display:flex;justify-content:space-between}.footer-nav{display:flex;flex-direction:column;margin-bottom:88px}.footer-nav-link{font-weight:normal;font-size:17px;line-height:20px;text-decoration:none;color:#fff;margin-bottom:20px}.footer-nav-link:last-child{margin-bottom:0}.footer-nav-text{width:366px;font-weight:normal;font-size:17px;line-height:20px;color:#fff}.footer-text{font-weight:300;font-size:14px;line-height:14px;color:#fff;margin-bottom:14px}.text-yellow{color:#ede852}

.air-q{padding:50px 0}.air-q-title{text-align:center;font-weight:normal;font-size:35px;line-height:41px;color:#fff}.air-q-map{height:560px;margin:42px 0}.air-q-text{font-weight:normal;font-size:23px;line-height:27px;color:#fff}

/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
  body {
    overflow-x: hidden;
  }

  /* HERO */

  .hero {
    min-height: 100vh;
    padding: 0;
  }

  .hero-partners img {
    width: 100%;
    max-width: 70px;
  }

  .hero-partners-item:first-child {
    margin-left: 10px;
  }

  .hero-partners-item {
    margin-left: 20px;
  }

  .hero-navigation {
    display: none;
  }

  .hero-title {
    width: 100%;
    /* max-width: 340px; */
    font-style: normal;
    font-weight: normal;
    font-size: 30px;
    line-height: 37px;
    padding: 140px 20px;
  }

  .hero-link {
    width: 100%;
    padding: 24px 0;
    font-size: 28px;
    line-height: 33px;
    text-align: center;
  }

  /* FACTS */

  .facts {
    padding: 70px 40px 50px;
  }

  .facts-title {
    font-size: 27px;
    line-height: 32px;
  }

  .facts-slide-desc {
    width: 100%;
    max-width: 280px;
    font-size: 17px;
    line-height: 20px;
  }

  .facts-pagination {
    margin-top: 0;
  }

  .facts-pagination-bullet {
    width: 12px;
    height: 12px;
  }

  /* AIR-Q */

  .air-q {
    padding: 0;
  }

  .air-q-title {
    font-size: 27px;
    line-height: 32px;
  }

  .air-q-map {
    margin: 26px 20px;
  }

  .air-q-text {
    font-size: 17px;
    line-height: 20px;
    padding: 0 20px;
  }

  /* SECTORS */

  .sectors-title {
    font-size: 27px;
    line-height: 32px;
    margin-bottom: 33px;
  }

  .sectors-subtitle {
    display: none;
  }

  .sectors-list {
    flex-direction: column;
    
  }

  .sectors-list-item {
    width: 90%;
    margin: 20px;
  }

  .sectors-list-item-tooltip {
    max-width: 95%;
    left: -10px;
    bottom: -100px;
    padding: 16px;
    -webkit-clip-path: polygon(100% 0, 100% 100%, 0 100%, 0 100%, 0 0);
            clip-path: polygon(100% 0, 100% 100%, 0 100%, 0 100%, 0 0);
  }

  /* FOOTER */

  .footer-partners {
    justify-content: space-between;
    margin: 0;
  }

  .footer-partners-item {
    width: 100%;
    max-width: 70px;
    margin: 20px;
  }

  .footer-partners-item:first-child {
    margin-left: 20px;
  }

  .footer-partners-item:last-child {
    margin-right: 20px;
  }

  .footer-text-wrap {
    flex-direction: column;
  }

  .footer-nav {
    text-align: center;
  }

  .footer-nav-text {
    width: 100%;
    text-align: center;
  }

  .footer-text {
    margin-top: 50px;;
    text-align: center;
  }

}

